import { Schema } from 'mongoose';
import { schemaComposer } from 'graphql-compose';
import { convertSchemaToGraphQL } from 'graphql-compose-mongoose';

export interface ICreditScoreValue {
  _id?: any;
  provider: string;
  value: number;
  max: number;
}

export const creditScoreValueSchema = new Schema<ICreditScoreValue>(
  {
    provider: {
      type: String,
    },
    value: {
      type: Number,
    },
    max: {
      type: Number,
    },
  },
  { timestamps: true }
);

convertSchemaToGraphQL(
  creditScoreValueSchema,
  'CreditScoreValue',
  schemaComposer
);
